.cart__section{
    position: relative;
    top: 60px;
    width: 100%;
    height: 80vh;
    /* background-color: rgb(245, 239, 239); */
}
.cart__section .cart__container{
    width: 80%;
    margin:0 auto;
    display: flex;
    padding:50px 20px;
}
.cart__container .left__cart{
    flex: .5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.left__cart img{
    width:50%;
    margin-bottom: 30px;
}

.left__cart .cart__btn{
    display: flex;
    justify-content:flex-start;
}

.cart__btn button{
    margin-right: 20px;
    padding: 10px 30px;
    cursor: pointer;
}
.cart__btn .cart__btn1{
    background-color:#FFD814;
    border: none;
    outline: none;
    border-radius: 20px;
    color: #0F1111;
    width: 200px;
    letter-spacing: .5px;
    font-size: 14px;
    box-shadow: 0 0 10px -5px rgba(0,0,0,.5);
}

.cart__btn .cart__btn1:hover{
    background-color:#f1cc13;
}

.cart__btn .cart__btn2{
    background-color: #FFA41C;
    letter-spacing: .5px;
    font-size: 14px;
    color: #0F1111;
    border: none;
    outline: none;
    border-radius: 20px;
    width: 200px;
    box-shadow: 0 0 10px -5px rgba(0,0,0,.5);
}
.cart__btn .cart__btn2:hover{
    background-color: #ec9615;
}
.cart__container .right__cart{
    flex: 0.5;
    border: 1px solid #c4c4c4;
    padding: 10px;
    border-radius: 4px;
}
.right__cart .mrp{
    margin-top: 10px;
}

.right__cart p{
    color: #565959;
    font-size: 14px;
    margin-bottom: 8px;
}
.right__cart h4{
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 15px;
}
.discount__box h5{
    color: #B12704;
    font-weight: 500;
    font-size: 16px;
}
.discount__box h4{
    font-size: 15px;
    margin-top: 10px;
    color: #007185;
}
.right__cart .description{
    font-weight: 600;
    color: #111;
    font-size: 16px;
    margin-top: 3px;
}

@media (max-width:778px){
    .cart__section{
        height: auto;
    }
    
    .cart__section .cart__container{
      flex-direction: column;
      align-items: center;
    }
    
    .cart__container .left__cart,
    .cart__container .right__cart{
        flex: 1;
    }
    .cart__container .right__cart{
        margin-top: 20px;
    }
}

@media (max-width:500px){
    .cart__btn .cart__btn1,
    .cart__btn .cart__btn2{
        width: 160px;
    }

    
.left_cart .cart_btn{
    justify-content: center;
}
}


