.login {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
  }
  .login__container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}
.login__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
  }
  .login__container >form > h5 {
    margin-bottom: 5px;
}
.login__container > form > input {
  width: 100%;
  height: 31px;
 
  border-radius: 3px;
  outline: none;
  border:1px solid #a6a6a6;
  margin-bottom: 10px;
  margin-top: 1px;
  }
 
 
 .login__signInButton {
  margin-top: 7px;
  margin-bottom: 23px;
  width: 100%;
  background: #f3d078;
  cursor: pointer;
  border:1px solid #9c7e31;
  height: 29px;
  outline: none;
  border-radius: 3px;
  color: #111;
  background: linear-gradient(to bottom,#f7dfa5,#f0c14b)
  }
  .login__registerButton {
    border-radius: 2px;
    width: 300px;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
    outline: none;
    margin-top: 15px;
    cursor: pointer;
  }
  .create__accountInfo  p{
    font-size: 12px;
    text-align: center;
    color: #767676;
    margin-top: 20px;
    font-weight: 600;

  }
  .below__SignUp {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    margin: 20px;
  }
  .below__SignUp > p{
    font-weight: light;
    font-size: 15px;
    margin: 2px 2px;
  }
  .below__SignUp NavLink{
    text-decoration: none;
    color: rgb(7, 116, 167);
    
  }


