.product{
   display: flex;
   flex-direction: column; 
   align-items: center;
   justify-content: flex-end;
   margin: 5px 5px;
   padding: 20px;
   width: 100%;
   max-height: 400px;
   min-width: 100px;
   background-color: white;
   z-index: 1;
   box-shadow: 0 1px 2px rgba(0,0,0,0.15);
   transition: all 0.3s ease-in-out;
   border-radius: 5px;
   position: relative;

   
}
.product::after{
  content: '';
  position: absolute;
  z-index: -1;
  width: 100%;
  max-height: 400px;
  opacity: 0;
  border-radius: 5px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  transition: opacity 0.3s ease-in-out;

}

.product:hover{
  transform: scale(1.09, 1.09);
  cursor: pointer;
  
}
.product:hover::after{
  opacity: 1;
}
.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 12px;
}
.product__price {
    margin-top: 5px;
  }
  .product__rating {
    display: flex;
  }
  
  .product__info {
    height: 100px;
    margin-bottom: 15px;
  }
  .product .btn {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    margin-bottom: 8px;
  }
  .product .btn:hover{
    box-shadow: 0.5px 0.5px 1px #202020;
  }

  @media (max-width:768px){
    .product{
     width: 100%;
     padding:0;
     max-height: 400px;
     margin: 8px 5px;
    
      }
      .product > img{
        max-height: 100px;
      }
      .product::after{
        max-height: 200px;
      }
      .product__info p{
        font-size: 14px;
      }
      .product .btn{
        font-size: 8px;
        font-weight: 500;
      }
  }