.product__section{
    width: 90vw;
    margin:10px auto;
    background-color: #fff;
    border-radius: 4px;
    padding: 10px 20px;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
    z-index: 1;
   
}

.product__section a{
    text-decoration: none;
    color: black;
}

.product__items{
    display: flex;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    margin-top: 10px;
    padding: 10px;
    justify-content: space-between;
}
.product__deal{
   display: flex;
   margin-bottom: 40px;
   margin-top: 10px;
  
}
.product__deal h3{
    font-weight: 600;
    font-size: 25px;
}

.deal__btn{
    background-color: #2874f0;
    color: #fff;
    cursor: pointer;
    border: 1px solid rgb(83, 110, 121);
    outline: none;
    border-radius: 4px;
    margin: 0 15px;
}
.product__img img{
    width: 150px;
    height: 150px;
    margin-top: 4px;
}
@media (max-width:768px){
    .product__section{
        width:96vw;
       overflow: hidden;
       margin: 0;
       margin-left: auto;
       margin-right: auto;
       padding: 10px 0;
    }
    .product__img img{
        width: 100px;
    height: 100px; 
    }
    .product__items{
    margin-top: 5px;
    padding: 4px;
    }
    .product__deal{
        margin-bottom: 20px;
    }
    .product__deal h3{
        font-size: 18px;
    }
    .deal__btn{
        font-size: 11px;
        
    }
    .product__items p{
        font-size: 10px;
    }
    
   
}