
  .header{
    display: flex;
  align-items: center; 
  background-color: #131921;
  width:100%;
  height: 60px;
position: sticky;
position:fixed;
  top: 0;
  z-index:100;
    
  }
  .header__logo {
    width: 100px;
    object-fit: contain;
    margin: 0 40px;
    margin-top: 18px;
  }
  .header__search {
    display: flex;
    flex:1 ;
    align-items: center;
    border-radius: 24px;
    
  }
  .header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 90%;
    border-bottom-left-radius: 3px;
    border-top-left-radius: 3px;
    cursor: pointer;
  }
  .header__searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
    border-bottom-right-radius: 3px;
    border-top-right-radius: 3px;
  }
  .header__optionLineOne {
    font-size: 10px;
  }
  
  .header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
  }
  
  .header__optionCart {
    display: flex;
    align-items: center;
    color: white;
  }
  
  .header__optionCart {
    margin-left: 10px;
    margin-right: 10px;
  }
  .header__nav {
    display: flex;
    justify-content: space-evenly;
  }
  .header__optionLineThree{
    font-size: 10px;
    margin-top: 12px;
  }
  .header__optionFour{
    font-size: 13px;
    font-weight: 800;

  }
  
  .header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
   
  }
  header .hamburger{
    display: none;
  }
  .header__option a{
    text-decoration: none;
    color: #fff;
    border: 1px solid #131921;
    border-radius: 2px;
    padding:  6px;
   
  }
  .header__option a:hover{
    border: 1px solid white;
  }
  .header__optionCart a{
    text-decoration: none;
    color: #fff;
    border: 1px solid #131921;
    border-radius: 2px;
    padding: 2px 6px;
  }
  .header__optionCart a:hover{
    border: 1px solid white;

  }
  .header__optionCart p{
    text-decoration: none;
  }
  header nav .avtar{
    cursor: pointer;
    background-color: #9a13cf;
    margin: 10px;
  }
  header nav .avtar1{
    cursor: pointer;
    margin: 10px;
  }
  header .extraSearch{
    position: absolute;
    background-color: #fff;
    color: #000;
    top: 50px;
    width: 440px;
    z-index: 10;
  }
  header .extraSearch a{
    text-decoration: none;
    color: #000;
  }



@media (max-width:800px){
  .header {
    position: fixed;
    width: 100vw;
    overflow: auto;

  }
  .header__search {
    display:none;
      }
      .header__nav {
        justify-content:flex-end;
       margin-left: 70px;
       margin-right: 10px;

      }
  
  header .hamburger{
      display: block;
  }
  
  header nav .header__option,
  header nav  .avtar
 {
      display:none;
  }
  .header__logo{
    width: 85px;

  }
  

  

  /* header nav .right .cart_btn{
      margin-right: 0;
  }
   */
  
};
@media (max-width:570px){

  
   

  header nav .header__logo img{
      width: 95px;
      margin-top: 14px;
      margin-left: 67px;
  }
}

